import React from "react";



const Header = () => {
    const [showMenu, setShowMenu] = React.useState(false);

    const linkAction = () => {
        setShowMenu(false);
    }

    const scrollActive = () => {
        const scrollY = window.pageYOffset;
        const sections = document.querySelectorAll("section[id]");  

        sections.forEach(current => {
            const sectionHeight = current.offsetHeight;
            const sectionTop = current.offsetTop - 50;
            const sectionId = current.getAttribute("id");
            if (scrollY > sectionTop && scrollY <= sectionTop + sectionHeight) {
                document.querySelector('.nav__menu a[href*=' +  sectionId + ']').classList.add("active-link");
            } else {
                document.querySelector('.nav__menu a[href*=' +  sectionId + ']').classList.remove("active-link");
            }
        })
    }

    React.useEffect(() => {
        const navLink = document.querySelectorAll(".nav__link");
        navLink.forEach(n => n.addEventListener("click", linkAction));
        
        window.addEventListener("scroll", scrollActive)

        return () => {
            window.removeEventListener("scroll", scrollActive);
            navLink.forEach(n => n.removeEventListener("click", linkAction));
        }
    }, []);

    return (
        <header className="l-header" id="header">
            <nav className="nav bd-container">
                <a href="#/" className="nav__logo">Aymen Hamada</a>
                <div className={`nav__menu ${showMenu ? 'show-menu' : ''}`} id="nav-menu">
                    <ul className="nav__list">
                        <li className="nav__item">
                            <a href="#home" className="nav__link active-link">
                                <i className="bx bx-home nav__icon"></i>
                                Home
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#profile" className="nav__link">
                                <i className="bx bx-user nav__icon"></i>
                                Profile
                            </a>
                        </li>
                        <li className="nav__item">

                            <a href="#education" className="nav__link">
                                <i className="bx bx-book nav__icon"></i>
                                Education
                            </a>
                        </li>
                        <li className="nav__item">

                            <a href="#skills" className="nav__link">
                                <i className="bx bx-receipt nav__icon"></i>
                                Skills
                            </a>
                        </li>
                        <li className="nav__item">

                            <a href="#experience" className="nav__link">
                                <i className="bx bx-home nav__icon"></i>
                                Experience
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#certificates" className="nav__link">
                                <i className="bx bx-award nav__icon"></i>
                                Certificates
                            </a>
                        </li>
                    </ul>
                </div>
                <div className="nav__toggle" id="nav-toggle" onClick={() => setShowMenu(!showMenu)}>
                    <i className="bx bx-grid-alt"></i>
                </div>
            </nav>

        </header>
    )
}

export default Header;