import React from "react";
import profilePicture from "../assets/img/profile-pic.png";
import AymenHamadaCV from "../assets/pdf/Aymen Hamada - Back End Developer - Resume.pdf";
import ProblemSolving from "../assets//icon/problem-solving.png";

const Home = () => {

    React.useEffect(() => {
        const themeButton = document.getElementById("theme-button");
        const darkTheme = "dark-theme";
        const iconTheme = "bx-sun";

        const selectedTheme = localStorage.getItem("selected-theme");
        const selectedIcon = localStorage.getItem("selected-icon");

        const getCurrentTheme = () => document.body.classList.contains(darkTheme) ? "dark" : "light";
        const getCurrentIcon = () => themeButton.classList.contains(iconTheme) ? "bx-moon" : 'bx-sun';

        if (selectedTheme) {
            document.body.classList[selectedTheme === 'dark' ? 'add' : 'remove'](darkTheme);
            themeButton.classList[selectedIcon === "bx-moon" ? 'add' : 'remove'](iconTheme);
        }

        const changeTheme = () => {
            document.body.classList.toggle(darkTheme);
            themeButton.classList.toggle(iconTheme);
            localStorage.setItem("selected-theme", getCurrentTheme());
            localStorage.setItem("selected-icon", getCurrentIcon());
        }
        themeButton.addEventListener("click", changeTheme);

        return () => {
            themeButton.removeEventListener("click", changeTheme);
        }

    }, []);

    return (
        <main className="l-main bd-container">
            <div className="resume" id="area-cv">
                <div className="resume__left">
                    <section className="home" id="home">
                        <div className="home__container section bd-grid">
                            <div className="home__data bd-grid">
                                <img src={profilePicture} alt='Aymen Hamada' className="home__img" />
                                <h1 className="home__title">Aymen <b>HAMADA</b></h1>
                                <h3 className="home__profession">Full Stack Developper</h3>

                                <div>
                                    <a download href={AymenHamadaCV} className="home__button-movil">Download</a>
                                </div>
                            </div>

                            <div className="home__address bd-grid">
                                <span className="home__information">
                                    <i className="bx bx-phone home__icon"></i> +33782282356
                                </span>
                                <span className="home__information">
                                    <i className="bx bx-envelope home__icon"></i> aymen.hamada@epitech.eu
                                </span>
                                <span className="home__information">
                                    <i className="bx bx-laptop home__icon"></i>
                                    <a href="https://aymenhamada.github.io/Portfolio/" target="__blank" rel="noreferrer">https://aymenhamada.github.io/Portfolio/</a>
                                </span>
                                <span className="home__information">
                                    <i className="bx bx-map home__icon"></i> Strasbourg, France
                                </span>


                            </div>
                        </div>
                        <i className="bx bx-moon change-theme" title="Theme" id="theme-button"></i>
                        <a download href={AymenHamadaCV}>
                            <i className="bx bx-download generate-pdf" title="Download CV" id="resume-button"></i>
                        </a>
                    </section>

                    <section className="social section">
                        <h2 className="section-title">SOCIAL</h2>

                        <div className="social__container bd-grid">
                            <a href="https://www.linkedin.com/in/aymen-hamada/" target="_blank" rel="noreferrer" className="social__link">
                                <i className="bx bxl-linkedin-square social__icon"></i>
                                Linkedin
                            </a>
                            <a href="https://github.com/aymenhamada" target="_blank" rel="noreferrer" className="social__link">
                                <i className="bx bxl-github social__icon"></i>
                                Github
                            </a>
                        </div>
                    </section>
                    <section className="profile section" id="profile">
                        <h2 className="section-title">Summary</h2>

                        <p className="profile__description">Back End Developer with 1 year of experience developping and maintaining PHP/Symfony4/Node.js web applications and micro-services.</p>
                    </section>
                    <section className="education section" id="education">
                        <h2 className="section-title">Education</h2>

                        <div className="education__container bd-grid">
                            <div className="education__content">
                                <div className="education__time">
                                    <span className="education__rounder"></span>
                                    <span className="education__line"></span>
                                </div>

                                <div className="education__data bd-grid">
                                    <h3 className="education__title">Master’s Degree in Software Engineering</h3>
                                    <span className="education__studies">Epitech</span>
                                    <span className="education__year">2024</span>
                                    <span className="education__studies">Software Engineering</span>
                                </div>
                            </div>
                            <div className="education__content">
                                <div className="education__time">
                                    <span className="education__rounder"></span>
                                    <span className="education__line"></span>
                                </div>

                                <div className="education__data bd-grid">
                                    <h3 className="education__title">BTEC Higher National Diploma</h3>
                                    <span className="education__studies">Epitech</span>
                                    <span className="education__year">2021</span>
                                    <span className="education__studies">Web development</span>
                                    <span className="education__studies">3.8 GPA.</span>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="skills section" id="skills">
                        <h2 className="section-title">Skills</h2>

                        <div className="skills__content bd-grid">
                            <ul className="skills__data">
                                <li className="skills__name">
                                    <i className="bx bxl-javascript home__icon"></i> JavaScript (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-nodejs home__icon"></i> Node.js (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-php home__icon"></i> PHP (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="fab fa-symfony home__icon"></i> Symfony (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bx-data home__icon"></i> SQL (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bx-data home__icon"></i> No-SQL (advanced)
                                </li>
                            </ul>
                            <ul className="skills__data">
                                <li className="skills__name">
                                    <i className="bx bxl-react home__icon"></i>
                                    <i className="bx bxl-redux home__icon"></i>
                                    React.js (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-html5 home__icon"></i> HTML (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-css3 home__icon"></i> CSS (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-git home__icon"></i> Git (advanced)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bx-git-compare home__icon"></i> Agile (Intermediate)
                                </li>
                                <li className="skills__name">
                                    <i className="bx bxl-react home__icon"></i> React Native (Intermediate)
                                </li>
                            </ul>
                        </div>
                    </section>
                </div>
                <div className="resume__right">
                    <section className="experience section" id="experience">
                        <h2 className="section-title">Experience</h2>
                        <div className="experience__container bd-grid">
                            <div className="experience__content">
                                <div className="experience__time">
                                    <span className="experience__rounder"></span>
                                    <span className="experience__line"></span>
                                </div>

                                <div className="experience__data bd-grid">
                                    <h3 className="experience__title">Back End Developer</h3>
                                    <span className="experience__company">Data Projekt</span>
                                    <span className="education__year">10/2020 - 10/2021</span>
                                    <ul className="skills__data">
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Integration of front‑end element created by front‑end
                                            developers
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Work closely with Project Manager and Team Leads on
                                            change request functions
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Develops new and maintains existing applications

                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Develop, troubleshoot, debug, test, ensure browser
                                            compatibility

                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Built reusable code and libraries for future use
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Migrated 3 nativescript mobile apps with code refactoring to
                                            an React Native based apps.
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span>Develop web API's and writing usage documentation using
                                            Swagger.
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Successfully scraped old website data who was using XML
                                            fles as a database, to migrate to an SQL server.
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Integrated various third party API’s using HTTP/SOAP/XML
                                        </li>
                                        <li className="skills__name">
                                            <span className="skills__circle"></span> Develop WordPress plugin using React.js and
                                            Node.js/Symfony 4 for an micro‑services API.
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="experience__content">
                                <div className="experience__time">
                                    <span className="experience__rounder"></span>
                                    <span className="experience__line"></span>
                                </div>

                                <div className="experience__data bd-grid">
                                    <h3 className="experience__title">HAMADA IT</h3>
                                    <span className="experience__company">Freelance</span>
                                    <span className="education__year">10/2021</span>
                                </div>
                            </div>
                        </div>
                    </section>
                    <section className="certificate section" id="certificates">
                        <h2 className="section-title">Certificates</h2>

                        <div className="certificate__container bd-grid">
                            <div className="certificate__content">
                                <div className="d-flex">
                                    <i className="fab fa-hackerrank home__icon"></i>
                                    <h3 className="certificate__title">JavaScript Intermediate</h3>
                                </div>
                                <span className="education__year">09/2021 - Present</span>
                            </div>
                            <div className="certificate__content">
                                <div className="d-flex">
                                    <i className="fab fa-hackerrank home__icon"></i>
                                    <h3 className="certificate__title">Rest API intermediate</h3>
                                </div>
                                <span className="education__year">09/2020 - Present</span>
                            </div>
                            <div className="certificate__content">
                                <div className="d-flex">
                                    <div className="d-flex column-centered">
                                        <div className="d-flex">
                                            <i className="fab fa-hackerrank home__icon"></i>
                                            <h3 className="certificate__title">Problem Solving 5 Stars</h3>
                                        </div>
                                    </div>
                                    <img src={ProblemSolving} alt="Problem Solving 5 stars - Hackerrank" className="problem__img" />
                                </div>
                                <p className="certificate__description">Problem solving stars are gained throught resolving hackerrank problem</p>
                                <span className="education__year">Lifetime</span>
                            </div>
                            <div className="certificate__content">
                                <div className="d-flex">
                                    <i className="fab fa-free-code-camp home__icon"></i>
                                    <h3 className="certificate__title">APIs and Microservices</h3>
                                </div>
                                <span className="education__year">09/2020 - Present</span>
                            </div>
                            <div className="certificate__content">
                                <div className="d-flex">
                                    <i className="fab fa-free-code-camp home__icon"></i>
                                    <h3 className="certificate__title">JavaScript Algorithms and Data Structures</h3>
                                </div>
                                <span className="education__year">09/2020 - Present</span>
                            </div>
                        </div>
                    </section>
                    <section className="languages section">
                        <h2 className="section-title">Languages</h2>

                        <div className="languages__container">
                            <ul className="languages__content bd-grid">
                                <li className="languages__name">
                                    <span className="languages__circle"></span> French (Native)
                                </li>
                                <li className="languages__name">
                                    <span className="languages__circle"></span> English (Fluent)
                                </li>
                            </ul>
                        </div>
                    </section>

                    <section className="interests section">
                        <h2 className="section-title">Interests</h2>

                        <div className="interests__container bd-grid">
                            <div className="interests__content">
                                <i className="bx bx-headphone interests__icon"></i>
                                <span className="interests__name">Music</span>
                            </div>
                            <div className="interests__content">
                                <i className="bx bxs-plane-alt interests__icon"></i>
                                <span className="interests__name">Travel</span>
                            </div>
                            <div className="interests__content">
                                <i className="bx bx-dumbbell interests__icon"></i>
                                <span className="interests__name">Fitness</span>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </main>
    )
}

export default Home;